import React from 'react';
import classNames from './Keypad.module.css';

const Keypad = () => {
    return (
        <div className={classNames.container}>
            <div className={classNames.digits}>
                <button className={classNames.digit}>7</button>
                <button className={classNames.digit}>8</button>
                <button className={classNames.digit}>9</button>
                <button className={classNames.digit}>4</button>
                <button className={classNames.digit}>5</button>
                <button className={classNames.digit}>6</button>
                <button className={classNames.digit}>1</button>
                <button className={classNames.digit}>2</button>
                <button className={classNames.digit}>3</button>
                <button className={classNames.digit}>
                    <i className="fas fa-times"></i>
                </button>
                <button className={classNames.digit}>0</button>
                <button className={classNames.digit}>
                    <i className="fas fa-trash-alt"></i>
                </button>
            </div>
        </div>
    );
};

export default Keypad;